* {
    &::-webkit-scrollbar {
        display: none;
    }
    & {
        -ms-overflow-style: none;
    }
}

*:focus {
    outline: none;
}

body {
    margin: 0;
    overflow: hidden;
}

html, body, .contents {
    height: 100%;
}

textarea:focus, input:focus {
    outline: none;
}

a {
    text-decoration: none;
}